// Tabs
.nav-tabs-bottom {
  border-top: $nav-tabs-border-width solid $nav-tabs-border-color;
  border-bottom: none;

  .nav-link {
    margin-top: -$nav-tabs-border-width;
    margin-bottom: 0;
    @include border-top-radius(0);
    @include border-bottom-radius($nav-tabs-border-radius);
  }

  .nav-link.active,
  .nav-item.show .nav-link {
    border-color: $nav-tabs-link-active-bg $gray-300 $gray-300;
  }
}

.nav-tabs.flex-column {
  border: none;
  border-right: $nav-tabs-border-width solid $nav-tabs-border-color;

  .nav-link {
    margin-right: -$nav-tabs-border-width;
    margin-bottom: 0;
    @include border-top-radius(0);
    @include border-start-radius($nav-tabs-border-radius);
  }

  .nav-link.active,
  .nav-item.show .nav-link {
    border-color: $gray-300 $nav-tabs-link-active-bg $gray-300 $gray-300;
  }
}

.nav-tabs-bottom.flex-column {
  border: none;
  border-left: $nav-tabs-border-width solid $nav-tabs-border-color;

  .nav-link {
    margin-left: -$nav-tabs-border-width;
    margin-bottom: 0;
    @include border-top-radius(0);
    @include border-end-radius($nav-tabs-border-radius);
  }

  .nav-link.active,
  .nav-item.show .nav-link {
    border-color: $gray-300 $gray-300 $gray-300 $nav-tabs-link-active-bg;
  }
}

.nav.flex-column {
  .nav-link {
    white-space: nowrap;
  }
}

.wordtrap-tabs {
  .nav-link {
    cursor: pointer;
  }

  &.d-flex {
    .tab-content {
      width: 100%;
    }
  }

  .tab-pane {
    > *:last-child {
      margin-bottom: 0;
    }
  }
}

.wordtrap-accordion {
  .accordion-body {
    > *:last-child {
      margin-bottom: 0;
    }
  }
}

.wordtrap-icon-box {
  display: flex;
  align-items: start;
  overflow: hidden;

  .icon-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }

  .content-wrap {
    text-align: left;
  }

  &,
  .icon-wrap,
  .box-title,
  .box-content {
    @include transition(all 0.2s);
  }

  &.icon-box-center {
    flex-direction: column;
    align-items: center;

    .content-wrap {
      text-align: center;
    }
  }

  &.icon-box-right {
    flex-direction: row-reverse;

    .content-wrap {
      text-align: right;
    }
  }
}

.wordtrap-slider {
  .slide {
    position: relative;

    &:after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 1;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }

    &:hover {
      &:after,
      .slide-overlay-icon,
      .slide-overlay-image {
        opacity: 1;
      }
    }
  }

  .slide-overlay-icon,
  .slide-overlay-image {
    position: absolute;
    top: 50%;
    left: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-style: solid;
    opacity: 0;
    transform: translate(-50%, -50%);
    transition: opacity 0.3s ease-in-out;
    z-index: 2;
  }

  .slide-overlay-image {
    img {
      max-width: 100%;
      height: auto;
    }
  }
}

.wordtrap-banner {
  position: relative;

  &:after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 1;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }

  &:hover {
    &:after,
    .banner-overlay-icon,
    .banner-overlay-image {
      opacity: 1;
    }
  }

  .banner-overlay-icon,
  .banner-overlay-image {
    position: absolute;
    top: 50%;
    left: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-style: solid;
    opacity: 0;
    transform: translate(-50%, -50%);
    transition: opacity 0.3s ease-in-out;
    z-index: 2;
  }

  .banner-overlay-image {
    img {
      max-width: 100%;
      height: auto;
    }
  }
}

.wordtrap-search-form {
  .text input,
  .cat {
    @extend .form-control;
    @extend .d-inline-block;
    @extend .align-top;
    @extend .w-auto;
  }

  .btn {
    @extend .btn-primary;
  }

  .cat-xxl-none {
    @extend .d-xxl-none;
  }

  .cat-xl-none {
    @extend .d-xl-none;
  }

  .cat-lg-none {
    @extend .d-lg-none;
  }

  .cat-md-none {
    @extend .d-md-none;
  }

  .cat-sm-none {
    @extend .d-sm-none;
  }
}
